import React from 'react';
import fetch from 'isomorphic-unfetch'


const Noticia = (props) => {

    const {source, title, description, urlToImage, url} = props.noticia
    let imagen

    if(urlToImage !== ''){
        imagen = <img  src={urlToImage} alt="Card image" className='card-img-top'/>
    }else{
        imagen = <p className = 'text-center my-5'>Imagen no Disponible</p>
    }

    
    
    console.log(props.imagenUrl);
    return (
        <div className = 'col-md-6 col-12'>
            <div className="card mb-3">                
                {imagen}
                <div className="card-body">
                    <h5 className="card-title">{title}</h5>                    
                </div>
                <div className="card-body">
                    <p className="card-text">{description}</p>
                    <p className="card-text">{source.name}</p>
                </div>                
                <div className="card-body">
                    <button href={url} target='_blank' type="button" className="btn btn-primary btn-lg btn-block">Leer Noticia</button>
                </div>                
            </div>
        </div>        
    );

   
};
Noticia.getInitialProps= async ()=> {
    const consultaActual = await fetch(urlToImage) 
    return {
        imagenUrl : consultaActual
    }
}


export default Noticia;